// src/pages/ProductionSupply.jsx

import React, { useState } from "react";
import axios from "axios";
import { Doughnut } from "react-chartjs-2";
import "chart.js/auto";

function ProductionSupply() {
  const [file, setFile] = useState(null);
  const [supplyData, setSupplyData] = useState([]);
  const [error, setError] = useState(null);
  const [selectedCategory, setSelectedCategory] = useState(null);

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
  };

  const handleUpload = async () => {
    if (!file) {
      setError(",  .");
      return;
    }

    setError(null);

    const formData = new FormData();
    formData.append("file", file);

    try {
      const response = await axios.post(
        "https://inotex-analytics.onrender.com/upload/", //   
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );

      setSupplyData(response.data.summary); // ,       summary
    } catch (err) {
      setError("  .   .");
    }
  };

  //     
  const categorizeData = (data) => {
    const low = [];
    const medium = [];
    const high = [];

    data.forEach((row) => {
      const percentage = parseFloat(row["% "]) || 0;

      if (percentage < 30) {
        low.push(row);
      } else if (percentage >= 30 && percentage < 60) {
        medium.push(row);
      } else if (percentage >= 60) {
        high.push(row);
      }
    });

    return { low, medium, high };
  };

  const getChartData = () => {
    const categories = categorizeData(supplyData);

    return {
      labels: [" ", " ", " "],
      datasets: [
        {
          data: [
            categories.low.length,
            categories.medium.length,
            categories.high.length,
          ],
          backgroundColor: [
            "rgb(252, 0, 0)",
            "rgb(255, 205, 86)",
            "rgb(0, 255, 42)",
          ],
          hoverOffset: 4,
        },
      ],
    };
  };

  const handleChartClick = (event, elements) => {
    if (elements.length > 0) {
      const index = elements[0].index;
      const categories = ["low", "medium", "high"];
      const selected = categories[index];
      const categorized = categorizeData(supplyData);
      setSelectedCategory(categorized[selected]);
    }
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold mb-4 text-center"></h1>

      {/*    */}
      <form
        onSubmit={(e) => {
          e.preventDefault();
          handleUpload();
        }}
        className="mb-4 flex flex-col md:flex-row gap-4 items-center justify-center"
      >
        <input
          type="file"
          onChange={handleFileChange}
          className="p-2 border rounded w-full md:w-1/3"
        />
        <button
          type="submit"
          className="bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600 w-full md:w-auto"
        >
          
        </button>
      </form>

      {error && <p className="text-red-500 text-center">{error}</p>}

      {/*   */}
      <div className="my-8">
        <h2 className="text-2xl font-bold mb-4 text-center">
             
        </h2>
        <div className="flex justify-center">
          <Doughnut
            data={getChartData()}
            options={{
              onClick: handleChartClick,
              plugins: {
                tooltip: {
                  callbacks: {
                    label: (context) =>
                      `${context.label}: ${context.raw} `,
                  },
                },
                title: {
                  display: true,
                  text: " ",
                },
              },
            }}
          />
        </div>
      </div>

      {/*   */}
      {selectedCategory && (
        <div className="mt-8">
          <h2 className="text-2xl font-bold mb-4 text-center"></h2>
          <table className="table-auto border-collapse w-full">
            <thead>
              <tr>
                <th className="border px-4 py-2"></th>
                <th className="border px-4 py-2">% </th>
                <th className="border px-4 py-2"></th>
                <th className="border px-4 py-2"> </th>
                <th className="border px-4 py-2"> </th>
              </tr>
            </thead>
            <tbody>
              {selectedCategory.map((row, index) => (
                <tr key={index}>
                  <td className="border px-4 py-2">
                    {row[" "]}
                  </td>
                  <td className="border px-4 py-2">
                    {row["% "]}
                  </td>
                  <td className="border px-4 py-2">{row[""]}</td>
                  <td className="border px-4 py-2">
                    {row[" "]}
                  </td>
                  <td className="border px-4 py-2">
                    {row[" "]}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}

export default ProductionSupply;
